[data-todoitem]{display:flex;align-items:center;justify-content:space-between}.todo-btn-group{display:flex;flex-wrap:nowrap}.todo-list-item{font-size:1.25rem}.todo-list-item button{width:35px;margin:3px}.todo-list-item-label{margin-left:1.25rem;line-height:35px;cursor:pointer;user-select:none}.todo-list-item.done .todo-list-item-label{text-decoration:line-through}.todo-list-item.important .todo-list-item-label{font-weight:bold;color:#dc3545}.todo-list-item.darkmode{background-color:black;color:white}.btn.btn-outline-danger.active{background-color:danger}.btn.btn-outline-danger:hover{background-color:white;color:#dc3545}.btn.btn-outline-danger:hover.darkmode{background-color:black}
